"use client";

import React, { useEffect } from "react";
import { motion, useAnimation, useScroll, useTransform } from "framer-motion";
import { useInView } from "react-intersection-observer";

const HomePage = () => {
  // Data voor de stappen
  const stappen = [
    {
      letter: "A",
      titel: "AMBITIE",
      beschrijving: "analyseren we en wordt concreet",
    },
    {
      letter: "B",
      titel: "AANPAK",
      beschrijving: "blijft bijgestelt worden",
    },
    {
      letter: "C",
      titel: "SBD",
      beschrijving: "gaat vooruitgang in komen",
    },
    {
      letter: "D",
      titel: "REFLECT",
      beschrijving: "doen we om stappen te blijven maken",
    },
  ];

  return (
    <main>
      {/* Andere secties van je homepage kunnen hier komen */}

      {/* Vier stappen sectie */}
      <div className="relative w-[100vw] min-h-[100vh] py-[10vw]">
        {/* Achtergrondafbeelding */}
        <div
          className="absolute inset-0 bg-center bg-cover bg-no-repeat opacity-90"
          style={{ backgroundImage: "url('/achtergrond.png')" }}
        ></div>

        {/* Content container */}
        <div className="relative z-10 w-[90vw] mx-auto">
          {/* Flex container voor de kolommen */}
          <div className="flex flex-col md:flex-row w-full">
            {stappen.map((stap, index) => (
              <StapItem
                key={stap.letter}
                letter={stap.letter}
                titel={stap.titel}
                beschrijving={stap.beschrijving}
                index={index}
                isLast={index === stappen.length - 1}
              />
            ))}
          </div>
        </div>
      </div>

      {/* Andere secties van je homepage kunnen hier komen */}
    </main>
  );
};

// StapItem component met animaties
const StapItem = ({ letter, titel, beschrijving, index, isLast }) => {
  const [ref, inView] = useInView({
    threshold: 0.3,
    triggerOnce: false,
  });

  // Animatie van de border lijn
  const borderControls = useAnimation();

  useEffect(() => {
    if (inView) {
      borderControls.start({
        height: "100%",
        transition: { duration: 0.8, ease: "easeInOut", delay: 0.2 },
      });
    } else {
      borderControls.start({
        height: "0%",
        transition: { duration: 0.4 },
      });
    }
  }, [borderControls, inView]);

  return (
    <div ref={ref} className={`relative w-full md:w-[25vw] px-[3vw] py-[4vw]`}>
      {/* Verticale border-line met animatie */}
      {!isLast && (
        <motion.div
          initial={{ height: "0%" }}
          animate={borderControls}
          className="absolute right-0 top-0 w-[0.1vw] bg-gray-300"
          style={{ originY: 0 }}
        />
      )}

      {/* Letter cirkel zonder hover animatie */}
      <div className="absolute top-[4vw] left-[3vw]">
        <motion.div
          initial={{ opacity: 0, scale: 0.8 }}
          animate={{
            opacity: inView ? 1 : 0,
            scale: inView ? 1 : 0.8,
            transition: { duration: 0.5, delay: index * 0.1 },
          }}
          className="rounded-full w-[5vw] h-[5vw] max-w-[5vw] max-h-[5vw] flex items-center justify-center border border-black"
        >
          <span className="font-medium text-[2vw] md:text-[1.2vw]">
            {letter}
          </span>
        </motion.div>
      </div>

      <div className="mt-[10vw]">
        <motion.h2
          initial={{ opacity: 0, x: -20 }}
          animate={{
            opacity: inView ? 1 : 0,
            x: inView ? 0 : -20,
            transition: { duration: 0.5, delay: 0.2 + index * 0.1 },
          }}
          className="text-[5vw] md:text-[3vw] font-black mb-[1vw]"
        >
          {titel}
        </motion.h2>

        <motion.p
          initial={{ opacity: 0, x: -10 }}
          animate={{
            opacity: inView ? 1 : 0,
            x: inView ? 0 : -10,
            transition: { duration: 0.5, delay: 0.3 + index * 0.1 },
          }}
          className="text-[2.5vw] md:text-[1.5vw]"
        >
          {beschrijving}
        </motion.p>
      </div>
    </div>
  );
};

export default HomePage;
